import {
  camera, scene, renderer, controls, // 3d 
  initCamera, initRenderer, initScene, initControls, // 3d
  render, global3D, // 3d 
  objects, targets, cardSize, // 3d 
} from './3d-core.js';
export {
  camera, scene, renderer, controls, // 3d 
  initCamera, initRenderer, initScene, initControls, // 3d
  render, global3D, // 3d 
  objects, targets, cardSize, // 3d 
}

import { transform, transformStatus, animate } from './3d-animate.js';
export { transform, transformStatus, animate };

import { create3DCard } from './3d-card-element.js';
import { targetsCoord } from './3d-card-coord.js';
import { initEvent } from './3d-bind-event.js';
export { rotateBall, rotateBallStop } from './3d-action.js';

function init() {
  initCamera(); // 
  initScene(); // 

  create3DCard(); // 3DDOM
  targetsCoord(); // tablespherehelixgrid

  initRenderer(); // 
  initControls(); // 

  // DOM TODO setTimeout
  setTimeout(() => {
    initEvent(); // 
  }, 100)
}

export { init };
